/* Corner ribbons */
.corner-ribbon {
  width: 200px;
  background: #4CAF50;
  position: absolute;
  top: 25px;
  left: -50px;
  text-align: center;
  line-height: 40px;
  color: #FFF;
  font-weight: 500;
  font-size: 14px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg); }

.corner-ribbon.sticky {
  position: fixed; }

.corner-ribbon.shadow {
  box-shadow: 0 0 10px rgba(38, 50, 56, 0.3); }

/* Different positions */
.corner-ribbon.top-left {
  top: 25px;
  left: -50px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg); }

.corner-ribbon.top-right {
  top: 20px;
  right: -60px;
  left: auto;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg); }

.corner-ribbon.bottom-left {
  top: auto;
  bottom: 25px;
  left: -50px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg); }

.corner-ribbon.bottom-right {
  top: auto;
  right: -50px;
  bottom: 25px;
  left: auto;
